@import url(https://fonts.googleapis.com/css2?family=Stylish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --sidebar_background: #222831;
  --white: #ffffff;
  --body_background: #393e46;
}
* {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
  color: white;
}
body {
  width: 100wh;
  text-align: center;
  height: 100vh;
  overflow: hidden;
}

.sidebar-body-container {
  display: flex;
}

.header {
  background-color: antiquewhite;
  width: 100wh;
  font-size: 3rem;
  text-align: left;
  display: flex;
}
.main-container {
  display: flex;
  flex-direction: column;
}

.body-main-container {
  display: flex;
  flex-wrap: wrap;
  background-color: var(--body_background);
  flex-basis: 70%;
  justify-content: center;
  align-items: flex-end;
}

.bars-container {
  margin-top: 10vh;
}
.container {
  position: relative;
  width: 600px;
  margin: auto;
  bottom: 5vh;
  display: flex;
}
.bar {
  height: 100px;
  background-color: black;
  font-size: 2rem;
  font-weight: bold;
  border-radius: 3px 3px 0 0;
  position: absolute;
  border: 1px solid black;
  bottom: 0;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  color: white;
}
.red {
  background-color: #ff0000;
}

.blue {
  background-color: blue;
}

.green {
  background-color: green;
}
.purple {
  border-top: 30px solid red;
}
.yellow {
  background-color: rgb(255, 165, 0);
}
.active {
  background-color: rgb(0, 183, 255);
  color: black;
}

.current {
  background-color: rgb(0, 183, 255);
  color: black;
}

.generate-new-array-btn-container {
  margin: 50px auto;
}

.sidebar-container {
  flex-basis: 30%;
}

.controller-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-basis: 30%;
  background-color: var(--sidebar_background);
  min-height: 90vh;
  border-top: 1px solid var(--body_background);
  padding-top: 30px;
}

.sliders-container {
  margin: 10% auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.github-logo-container {
  display: flex;
  flex-direction: column;
  margin: 10px auto;
  align-items: center;
}

.header-container {
  min-height: 30px;
  padding: 10px;
  background-color: #222831;
}
.header-heading-container {
  text-align: center;
}
.header-heading {
  font-family: "Stylish", sans-serif;
  font-size: 2rem;
}

